.bg-blue {
  background-color: #BAD9F0;
}

/*.feedback-section__container {

}*/

.rate-feedback-section {
  & + .rate-feedback-section {
    margin-top: calc(90px + (151 - 90) * ((100vw - 1200px) / (1920 - 1200)));
  }

  .grid {
    @include custom-grid-padding(rem(47));
  }

  .preview-column {
    max-width: none;

    .card {
      max-width: rem(647);
    }
  }

  .card-header {
    text-align: left;
    min-height: rem(176);
    color: inherit;
    padding: rem(20) rem(50);
    background-color: #fff;
    border-radius: rem(4) rem(4) 0 0;
  }

  .card-title {
    text-align: left;
    margin-bottom: rem(13);
  }

  .fb-options {
    display: flex;
    align-items: center;
    padding-top: 0;

    .column-inner {
      max-width: rem(483);
    }
  }

  .fb-options__item {
    & + .fb-options__item {
      margin-top: rem(53);
    }
  }

  .fb-options__title {
    justify-content: flex-start;
    margin-right: 0;
    margin-left: 0;
    margin-bottom: rem(23);
  }

  .options-holder {
    flex-wrap: nowrap;
    justify-content: center;
    margin: 0;

    .option-item {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: row;
      width: rem(80);
      max-width: rem(80);
      height: rem(48);
      color: #414141;
      padding: rem(3) rem(5);
      background-color: #fff;
      border-radius: rem(4);

      &.active {
        color: #fff !important;
        background-color: $success-color !important;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      }

      & + .option-item {
        margin-left: rem(21);
      }
    }
  }

  textarea {
    width: 100%;
    height: rem(131);
    padding: rem(11);
    border-radius: rem(4);
    background-color: #fff;
    border: none;

    &::-webkit-input-placeholder {
      color: $light-grey;
      font-style: italic;
    }

    &:-moz-placeholder { /* Firefox 18- */
      color: $light-grey;
      font-style: italic;
    }

    &::-moz-placeholder { /* Firefox 19+ */
      color: $light-grey;
      font-style: italic;
    }

    &:-ms-input-placeholder {
      color: $light-grey;
      font-style: italic;
    }
  }

  &.primary-view {
    .card-header {
      color: #fff;
      background-color: $primary-color;
    }

    .options-holder {
      .option-item {
        color: #084C79;
        background-color: #CEDAE9;
      }
    }

    textarea {
      background-color: #CEDAE9;

      &::-webkit-input-placeholder {
        color: #205392;
        font-style: italic;
      }

      &:-moz-placeholder { /* Firefox 18- */
        color: #205392;
        font-style: italic;
      }

      &::-moz-placeholder { /* Firefox 19+ */
        color: #205392;
        font-style: italic;
      }

      &:-ms-input-placeholder {
        color: #205392;
        font-style: italic;
      }
    }
  }
}