.submission-holder {
  position: relative;

  .bg-layer {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;

    & > *, &:before, &:after {
      position: absolute;
    }

    .puzzle-img {
      top: 50%;
      right: rem(106);
      transform: translateY(-50%);
      z-index: 2;

      img {
        max-width: calc(170px + (306 - 170) * ((100vw - 1200px) / (1920 - 1200)));
        max-height: 95vh;
        width: auto;
       /* object-fit: contain;
        max-height: 100%;*/
      }
    }

    &:before, &:after {
      content: '';
    }

    &:before {
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
      background: no-repeat url("../img/png/last-section-top-bg_2.png");
      background-size: 100%;
      z-index: 1;
    }

    &:after {
      right: calc(100px + (270 - 100) * ((100vw - 1200px) / (1920 - 1200)));
